import React from 'react'

export default function contact() {
    
function validation(){
    let name =document.getElementById("name").value
    let email =document.getElementById("email").value
    let phone =document.getElementById("phone").value

    if(name!==""){
document.getElementById("lapelName").classList.remove("green")
    }else{
document.getElementById("lapelName").classList.add("green")

    }

        if(email!==""){
                document.getElementById("lapelEmail").classList.remove("green")
            if(validateEmail()){
                document.getElementById("messageEmail").classList.add('d-none')
            }else{
                document.getElementById("messageEmail").classList.remove('d-none')
                }
        } else{
            document.getElementById("lapelEmail").classList.add("green")
        }



      if(phone!==""){
          document.getElementById("lapelPhone").classList.remove("green")
           if(validatePhone()){
                document.getElementById("messagePhone").classList.add('d-none')
            }else{
                document.getElementById("messagePhone").classList.remove('d-none')
                }
    }else{
document.getElementById("lapelPhone").classList.add("green")

    }
    
   


     function validateEmail() {
     var regex = /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/
     if (regex.test(email)) {
         return true
     } else {
         return false
     }
 }   function validatePhone() {
     var regex = /^\+{0,2}([\-\. ])?(\(?\d{0,3}\))?([\-\. ])?\(?\d{0,3}\)?([\-\. ])?\d{3}([\-\. ])?\d{4}/;
     if (regex.test(phone)) {
         return true
     } else {
         return false
     }
 }
}
    

    
  return (
    <>
        <section id='contact' className="py-4 mt-4">
         <div className="container py-5">
                  <div className='d-flex align-items-center flex-column mb-4'>
                     <h2 className="text-uppercase fw-bold mb-3">contact</h2>
                   <div className=' d-flex align-items-center'>
                  <div className="line"></div>
                   <i className="fas fa-star px-3 fs-2"></i>
                  <div className="line"></div>
                  </div>
                  </div>
                  <form action="">
                       <div className="control-group">
                                <div className=" mb-0 pb-2">
                                <label htmlFor="name" id='lapelName' className='green'>Name</label>
                                    <input id="name" className='form-control' onKeyUp={validation} type="text" placeholder="Name" required="required" />
                                    
                                </div>
                            </div>
                            <div className="control-group">
                                <div className=" mb-0 pb-2">
                                <label htmlFor="email" id='lapelEmail' className='green'>Email</label>
                                    <input id="email" className='form-control' onKeyUp={validation} type="email" placeholder="Email Address" required="required"  />
                                    <p className='text-danger d-none' id='messageEmail'>* Not a valid email address</p>
                                </div>
                            </div>
                            <div className="control-group">
                                <div className="mb-0 pb-2">
                                <label htmlFor="phone"  id='lapelPhone' className='green'>Phone</label>
                               
                                    <input id="phone" className='form-control' onKeyUp={validation} type="tel" placeholder="Phone Number" required="required"  />
                                          <p className='text-danger d-none' id='messagePhone'>* Not a valid Phone Number</p>
                                </div>
                            </div>
                            <div className="control-group">
                                <div className="mb-0 pb-2">
                                
                                    <textarea id="message" className='form-control' rows="4" placeholder="Message" required="required" ></textarea>
                                   
                                </div>
                            </div>
                            <button id="sendMessageButton" type="submit">Send</button>
                  </form>

                
                  </div>

            </section>
    </>
  )
}
