import React from 'react'
import { Link } from 'react-router-dom'
import "/node_modules/jquery/dist/jquery.min.js"

export default function navbar() {
 

function navPort (){
  document.getElementById("navPort").classList.add("active")

  document.getElementById("navContact").classList.remove("active")
  document.getElementById("navAbout").classList.remove("active")
  
}

function navAbout (){
  document.getElementById("navAbout").classList.add("active")


  document.getElementById("navPort").classList.remove("active")
  document.getElementById("navContact").classList.remove("active")

  
}
function navContact (){
  document.getElementById("navContact").classList.add("active")

  document.getElementById("navPort").classList.remove("active")
  document.getElementById("navAbout").classList.remove("active")
  
}

  return (
    <>
 <nav className="navbar navbar-expand-lg fixed-top">
  <div className="container">
    <Link className="navbar-brand fs-2 fw-bold text-white"  to="">START REACT</Link>
    <button className="navbar-toggler text-white fs-5 p-3" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent1" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
     <span className='fw-bold'>MENU</span>
      <i className="fa-solid fa-bars ms-1"></i>
    </button>
    <div className="collapse navbar-collapse" id="navbarSupportedContent1">
      <ul className="navbar-nav ms-auto mb-2 mb-lg-0">
      
        <li className="nav-item">
          <Link className="nav-link" id='navPort' onClick={navPort} to='portfolio'>PORTFOLIO</Link>
        </li>
        <li className="nav-item">
          <Link className="nav-link" id='navAbout' onClick={navAbout} to='about'>ABOUT</Link>
        </li>  <li className="nav-item">
          <Link className="nav-link" id='navContact' onClick={navContact} to='contact'>CONTACT</Link>
        </li>
      
      </ul>
   
    </div>
  </div>
</nav>
<script src="/node_modules/jquery/dist/jquery.min.js"></script>
    </>
  )
}
