
import React from 'react'



export default function portfolio() {
  
  function handClick(e){
  let  imgesSrc =e.target.src
  let name =e.target.name
document.getElementById("lightBoxContainer").classList.replace("d-none","d-flex")
document.getElementById("empty").innerHTML=`
                   <div class='d-flex align-items-center flex-column my-4'>
                     <h2 class="text-uppercase fw-bold pt-4">${name}</h2>
                   <div class=' d-flex align-items-center'>
                  <div class="line"></div>
                   <i class="fas fa-star px-3 fs-2"></i>
                  <div class="line"></div>
                  </div></div>                    
                        <img src=${imgesSrc} id='inner' alt="" />
                        <div class=" px-3 py-3">
                            <p class="pt-2 w-75 mx-auto">Lorem ipsum dolor sit amet, consectetur adipisicing elit.
                            Lorem ipsum dolor sit amet, consectetur adipisicing elit. Optio, Lorem ipsum dolor sit amet. consectetur?</p>
                     
                       </div>
                  
`
  };
function closeWindow(){
 document.getElementById("lightBoxContainer").classList.replace("d-flex","d-none")

}


  return (
    
    <>
    <section id='portfolio'>
         <div className="container py-5 mt-5">
                  <div className='d-flex align-items-center flex-column mb-4'>
                     <h2 className="text-uppercase fw-bold ">portfolio</h2>
                   <div className=' d-flex align-items-center'>
                  <div className="line"></div>
                   <i className="fas fa-star px-3 fs-2"></i>
                  <div className="line"></div>
                  </div></div>

        <div className="row gy-4">
            <div className="col-md-6 col-lg-4"  >
                <div className="item position-relative overflow-hidden">
                    <img  onClick={handClick} name="LOG CABIN"  src="http://routeegypt.com/start-react/assets/img/portfolio/cabin.png" className='w-100 rounded-4' alt="" />
                     <div  className='imagCaption  rounded-4'>
                  <i className="fa-solid fa-plus"></i>
                 </div> 
                 </div>
            </div>  <div className="col-md-6 col-lg-4">
                <div className="item position-relative overflow-hidden">
                    <img onClick={handClick} name="TASTY CAKE"  src="http://routeegypt.com/start-react/assets/img/portfolio/cake.png" className='w-100 rounded-4' alt="" />
                     <div className='imagCaption  rounded-4'>
                  <i className="fa-solid fa-plus"></i>
                 </div> </div>
            </div>  <div className="col-md-6 col-lg-4">
                <div className="item position-relative overflow-hidden">
                    <img onClick={handClick} name="CIRCUS TENT" src="http://routeegypt.com/start-react/assets/img/portfolio/circus.png" className='w-100 rounded-4' alt="" />
                     <div className='imagCaption  rounded-4'>
                  <i className="fa-solid fa-plus"></i>
                 </div> </div>
            </div>  <div className="col-md-6 col-lg-4">
                <div className="item position-relative overflow-hidden">
                    <img onClick={handClick} name="CONTROLLER"  src="http://routeegypt.com/start-react/assets/img/portfolio/game.png" className='w-100 rounded-4' alt="" />
                     <div className='imagCaption  rounded-4'>
                  <i className="fa-solid fa-plus"></i>
                 </div> </div>
            </div>  <div className="col-md-6 col-lg-4">
                <div className="item position-relative overflow-hidden">
                    <img onClick={handClick} name="LOCKED SAFE"  src="http://routeegypt.com/start-react/assets/img/portfolio/safe.png" className='w-100 rounded-4' alt="" />
                     <div className='imagCaption  rounded-4'>
                  <i className="fa-solid fa-plus"></i>
                 </div> </div>
            </div>
              <div className="col-md-6 col-lg-4">
                <div className="item position-relative overflow-hidden">
                    <img onClick={handClick} name="SUBMARINE" src="http://routeegypt.com/start-react/assets/img/portfolio/submarine.png" className='w-100 rounded-4' alt="" />
                     <div className='imagCaption  rounded-4'>
                  <i className="fa-solid fa-plus"></i>
                 </div> 
                 </div>
            </div>
         
        </div>
        </div>


       <div id="lightBoxContainer" className="d-none   justify-content-center align-items-center">
                <div id="lightBox" className="bg-white p-0 position-relative">
                    <i className="fa-solid fa-xmark  position-absolute top-0 end-0 m-2" onClick={closeWindow} id="closeBtn"></i>
                   <div id='empty'>

                   </div>
                   <button className='btn btn-success fs-6 border-0' onClick={closeWindow}><span><i className='fa-solid fa-xmark me-2'></i></span>Close Window</button>
                </div>
       </div>

      

    </section>
    </>
  )
}






